.header {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
  }
  &__logo {
    width: 311px;
    display: block;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__phone {
    display: none;
    font-size: 13px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.26px;
    color: #454545;
  }
  &__btn {
    height: 16px;
    display: none;
    padding: 2px 7px;
    span {
      font-size: 9px;
      letter-spacing: -0.18px;
    }
  }
  &__menu {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #503E1F 0%, #503E1F 100%);
    padding: 160px 15px 60px;
    color: #E3E3E3;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    text-align: center;
    li a {
      font-size: 36px;
      line-height: 120%;
      &:hover {
        color: #FDD057;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 71px;
  height: 31px;
  display: block;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 7px;
    background: #fff;
    border-radius: 10px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #E3E3E3 !important;
    }
    span:nth-child(1) {
      top: 12px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 11px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    padding: 40px 0;
    &__list {
      li a {
        font-size: 30px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    position: static;
    padding: 10px 0;
    &__logo {
      width: 86px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__phone {
      display: block;
      color: #454545;
    }
    &__btn {
      display: flex;
    }
    &__menu {
      padding: 80px 15px 40px;
    }
    &__list {
      gap: 15px;
      li a {
        font-size: 20px;
      }
    }
  }
  .burger {
    width: 26px;
    height: 11px;
    span {
      height: 2px;
      background: #525151;
    }
    &--active {
      span:nth-child(1) {
        top: 5px;
      }
      span:nth-child(3) {
        bottom: 4px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .header {
    &__btn {
      display: none;
    }
  }
}