.wrapper {
  position: relative;
  overflow: hidden;
}
.btn-orange {
  background: #C6981D;
  border-radius: 21px;
  color: #fff;
  &:hover {
    background: #C6981D;
    filter: brightness(90%);
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.btn-green {
  background: #4AD12F;
  box-shadow: 0px 0px 16.7px 0px #C6981D;
  border-radius: 21px;
  color: #fff;
  &:hover {
    background: #4AD12F;
    filter: brightness(90%);
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.btn-black {
  background: #505050;
  border-radius: 21px;
  color: #fff;
  &:hover {
    background: #505050;
    filter: brightness(90%);
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.h2 {
  font-size: 51px;
  line-height: 120%;
  font-weight: 500;
  text-transform: uppercase;
}
.form {
  &-group {
    input {
      width: 100%;
      height: 110px;
      background: transparent;
      border: 1px solid #D9D9D9;
      border-radius: 11px;
      padding: 0 35px;
      font-size: 45px;
      line-height: 120%;
      font-weight: 300;
      color: #fff;
      &::placeholder {
        color: rgba(255, 255, 255, 0.29);
      }
    }
  }
  &__btn {
    height: 89px;
    border-radius: 21px;
    padding: 5px 10px;
    span {
      font-size: 39px;
      letter-spacing: -.78px;

    }
  }
  &__check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:before {
        background-image: url("../img/check-icon.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 60px;
      font-size: 23px;
      line-height: 120%;
      &:before {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80% auto;
        border: 1px solid #D9D9D9;
        border-radius: 9px;
      }
      a {
        position: relative;
        z-index: 3;
        text-decoration: underline;
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(27, 29, 37, 0.45);
  padding: 50px 0;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 100px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 40px);
    max-width: 1000px;
    background: #fff;
    border-radius: 35px;
    padding: 79px 32px 32px 32px;
    margin: 0 auto;
  }
  &__close {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 32px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    &-group {
      input {
        border-color: #505050;
        color: #000;
        &::placeholder {
          color: #505050;
        }
      }
    }
  }
  &__title {
    text-align: center;
    margin-bottom: 30px;
  }
}
.modal2 {
  .modal__dialog {
    max-width: 780px;
    padding: 60px 30px;
  }
  .modal__title {
    margin-bottom: 18px;
  }
  .modal__img {
    max-width: 190px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  .modal__text {
    max-width: 545px;
    text-align: center;
    text-transform: unset;
    margin: 0 auto;
  }
}
.cookies {
  padding: 45px 0;
  &__inner {
    position: relative;
    padding-right: 63px;
  }
  &__text {
    font-size: 18px;
    line-height: 120%;
  }
  &__close {
    position: absolute;
    z-index: 10;
    top: -16px;
    right: 0;
    width: 33px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 1400px) {
  .h2 {
    font-size: 36px;
  }
  .form {
    &-group {
      input {
        height: 80px;
        padding: 0 20px;
        font-size: 30px;
      }
    }
    &__btn {
      height: 70px;
      border-radius: 15px;
      span {
        font-size: 28px;
        letter-spacing: -0.56px;
      }
    }
  }
  .modal {
    &__dialog {
      max-width: 800px;
    }
  }
  .cookies {
    padding: 35px 0;
    &__inner {
      padding-right: 50px;
    }
    &__text {
      font-size: 14px;
    }
    &__close {
      width: 28px;
    }
  }
}

@media screen and (max-width: 992px) {
  .form {
    &-group {
      input {
        height: 40px;
        border-radius: 8px;
        padding: 0 15px;
        font-size: 16px;
      }
    }
    &__btn {
      height: 36px;
      span {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
    &__check {
      label {
        padding-left: 45px;
        font-size: 16px;
      }
    }
  }
  .modal {
    &__dialog {
      max-width: 500px;
      border-radius: 15px;
      padding: 60px 24px 24px 24px;
    }
    &__close {
      top: 20px;
      right: 20px;
      width: 20px;
    }
    &__title {
      font-size: 28px !important;
      margin-bottom: 10px;
    }
    .form {
      gap: 10px;
    }
  }
  .modal2 {
    .modal__dialog {
      max-width: 315px;
      padding: 30px 15px;
    }
    .modal__title {
      font-size: 23px !important;
      margin-bottom: 10px;
    }
    .modal__img {
      max-width: 92px;
    }
    .modal__text {
      max-width: 215px;
      font-size: 19px;
    }
  }
  .cookies {
    padding: 24px 0;
    &__inner {
      padding-right: 45px;
    }
    &__text {
      font-size: 12px;
    }
    &__close {
      top: -5px;
      width: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .h2 {
    font-size: 20px;
  }
  .form {
    &-group {
      input {
        height: 25px;
        border-radius: 8px;
        padding: 0 12px;
        font-size: 9px;
      }
    }
    &__btn {
      height: 22px;
      span {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
    &__check {
      label {
        padding-left: 15px;
        font-size: 5.5px;
        &:before {
          width: 9px;
          height: 9px;
          border-radius: 2px;
        }
      }
    }
  }
  .modal {
    &__dialog {
      padding: 56px 20px 20px 20px;
    }
    .form {
      &-group {
        input {
          height: 40px;
          border-radius: 8px;
          padding: 0 15px;
          font-size: 16px;
        }
      }
      &__btn {
        height: 36px;
        span {
          font-size: 14px;
          letter-spacing: -0.28px;
        }
      }
      &__check {
        label {
          padding-left: 45px;
          font-size: 16px;
          &:before {
            width: 30px;
            height: 30px;
            border-radius: 9px;
          }
        }
      }
    }
  }
  .cookies {
    background: #D9D9D9;
    padding: 15px 0;
    &__inner {
      padding-right: 32px;
    }
    &__text {
      font-size: 10px;
    }
    &__close {
      top: 0;
      width: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .cookies {
    padding: 10px 0;
    &__text {
      font-size: 6px;
    }
    &__close {
      width: 14px;
    }
  }
}

@media screen and (max-width: 400px) {
  .h2 {
    font-size: 16px;
  }
}