.main {
  position: relative;
  background-image: url("../img/main-img.webp");
  background-repeat: no-repeat;
  background-position: right top;
  background-size: cover;
  padding: 164px 0 64px;
  color: #fff;
  &__title {
    font-size: 60px;
    line-height: 120%;
    font-weight: 600;
    text-shadow: 0px 4px 27.1px #000000;
    margin-bottom: 20px;
    span {
      display: inline-block;
      font-size: 83px;
      color: #FDD057;
      text-shadow: 0px 4px 27.1px rgba(0, 0, 0, 0.45);
    }
  }
  &__subtitle {
    font-size: 39px;
    line-height: 120%;
    font-weight: 600;
    text-shadow: 0px 4px 27.1px #000000;
    margin-bottom: 20px;
    span {
      display: inline-block;
      font-size: 83px;
      letter-spacing: 1.66px;
      color: #FDD057;
      text-shadow: 0px 4px 27.1px rgba(0, 0, 0, 0.45);
    }
  }
  &__text {
    position: relative;
    max-width: 630px;
    font-size: 39px;
    line-height: 120%;
    letter-spacing: -0.78px;
    text-shadow: 0px 4px 27.1px #000000;
    padding-bottom: 50px;
    margin-bottom: 80px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: calc(100% - 55px);
      height: 1px;
      background: #FDD057;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-bottom: 44px;
    li {
      position: relative;
      padding-left: 44px;
      font-size: 39px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: -0.78px;
      text-shadow: 0px 4px 27.1px #000000;
      &:before {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 36px;
        height: 36px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  &__btn {
    height: 89px;
    padding: 5px 21px;
    margin-bottom: 10px;
    span {
      font-size: 30px;
      letter-spacing: -0.6px;
      text-transform: uppercase;
    }
  }
  &__info {
    padding-left: 34px;
    font-size: 31px;
    line-height: 120%;
    letter-spacing: 4.34px;
  }
  &__founder {
    display: none;
  }
}
.info {
  background: linear-gradient(to right, #503E1F 0%, #503E1F 100%);
  padding: 115px 0 65px;
  color: #E3E3E3;
  &__title {
    text-align: center;
    margin-bottom: 115px;
  }
  &__video-wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 140px / 2));
    gap: 140px;
    margin-bottom: 100px;
  }
  .video {
    position: relative;
    padding-bottom: 52%;
    margin-bottom: 23px;
    video,
    &__link{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 11px;
      object-fit: cover;
      cursor: pointer;
    }
    &__link img {
      width: 100%;
      height: 100%;
      border-radius: 11px;
      object-fit: cover;
    }
    &__play {
      position: absolute;
      z-index: 5;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 164px;
      height: 164px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      pointer-events: none;
      img {
        width: 59px;
        margin-left: 15px;
      }
    }
    .hidden {
      display: none;
    }
    &__title {
      text-align: center;
      font-size: 39px;
      line-height: 120%;
      font-weight: 300;
      text-transform: uppercase;
    }
  }
  &__group {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding-top: 28px;
    margin-bottom: 90px;
  }
  .line {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 15px;
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 38px;
      height: 38px;
      background: #F3A400;
      border-radius: 50%;
    }
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 38px;
      height: 38px;
      background: #F3A400;
      border-radius: 50%;
    }
    span {
      max-width: 58px;
      height: 10px;
      flex-grow: 1;
      background: #F3A400;
    }
    span:nth-child(14) ~ span {
      display: none;
    }
  }
  &__start,
  &__finish {
    font-size: 36px;
    line-height: 120%;
    font-weight: 300;
  }
  &__period {
    font-size: 46px;
    line-height: 120%;
    font-weight: 300;
  }
  &__btn {
    height: 89px;
    padding: 5px 115px;
    margin: 0 auto;
    span {
      font-size: 39px;
      letter-spacing: -0.78px;
    }
  }
}
.advantages {
  padding: 160px 0 80px;
  .quote {
    color: #000;
    margin-bottom: 160px;
    &__text {
      text-align: center;
      font-style: italic;
      font-size: 55px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: -1.1px;
      margin-bottom: 60px;
    }
    &__author {
      text-align: right;
      font-size: 41px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.82px;
    }
  }
  &__title {
    text-align: center;
    margin-bottom: 68px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    margin-bottom: 85px;
  }
  &__cards2 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 120px / 2));
    gap: 120px;
  }
}
.advantage-card {
  position: relative;
  &:nth-child(1) {
    .advantage-card__img {
      img {
        border-radius: 24px 0 0 0;
      }
    }
  }
  &:nth-child(2) {
    .advantage-card__img {
      img {
        border-radius: 0 24px 0 0;
      }
    }
  }
  &:nth-child(3) {
    .advantage-card__img {
      img {
        border-radius: 0 0 0 24px;
      }
    }
  }
  &:nth-child(4) {
    .advantage-card__img {
      img {
        border-radius: 0 0 24px 0;
      }
    }
  }
  &__img {
    position: relative;
    padding-bottom: 66%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 45px 15px;
    color: #E3E3E3;
  }
  &__text {
    font-size: 30px;
    line-height: 120%;
    font-weight: 600;
    text-shadow: 0px 4px 27.1px #000000;
    margin-bottom: 10px;
  }
  &__text2 {
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
    text-shadow: 0px 4px 27.1px #000000;
  }
  &__btn {
    position: absolute;
    z-index: 3;
    top: 45px;
    left: 45px;
    gap: 8px;
    background: #fff;
    border-radius: 42px;
    padding: 7px 6px 7px 12px;
    span {
      font-size: 25px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: -0.5px;
      color: #414141;
    }
    i {
      width: 31px;
      height: 31px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EB4949;
      border-radius: 50%;
      img {
        width: 16px;
      }
    }
  }
}
