      font-size: 28px;
      letter-spacing: -0.56px;
      text-transform: uppercase;
    }
  }
  &__slider {
    display: none;
  }
}
.stages {
  padding: 80px 0 100px;
  color: #3D3939;
  &__title {
    text-align: center;
    margin-bottom: 130px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5));
    font-size: calc(1600px * (16 / 1600));
    @media screen and (max-width: 1600px) {
      font-size: calc(100vw * (16 / 1600));
    }
    @media screen and (max-width: 992px) {
      font-size: calc(100vw * (16 / 439));
    }
    @media screen and (max-width: 439px) {
      font-size: calc(100vw * (16 / 320));
    }
  }
  &__item {
    position: relative;
    &-group {
      position: relative;
      z-index: 2;
      width: 18.75em;
      aspect-ratio: 16 / 16;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      margin: 0 auto;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 20em;
        height: 20em;
        background: #fff;
        border-radius: 50%;
        pointer-events: none;
      }
      .stages__item-title {
        position: relative;
        z-index: 2;
        font-size: 1.3125em;
        line-height: 120%;
        font-weight: 600;
        letter-spacing: 0.0525em;
        text-transform: uppercase;
      }
      .stages__item-number {
        position: relative;
        z-index: 2;
        font-family: 'Buyan';
        font-size: 4.0625em;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
    &-group2 {
      position: relative;
      z-index: 3;
      aspect-ratio: 16 / 16;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 20em;
        height: 20em;
        background: #C6981D;
        border-radius: 50%;
        pointer-events: none;
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 17.813em;
        height: 17.813em;
        border: 0.5em solid rgba(255, 255, 255, 1);
        background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(255, 255, 255, 1) 38%,rgba(207.02, 207, 207, 1) 100%);
        box-shadow: 1.063em 0.875em 2.15em 0 rgba(0, 0, 0, 0.39);
        border-radius: 50%;
        pointer-events: none;
      }
      .stages__item-title {
        position: relative;
        z-index: 2;
        font-size: 1.563em;
        line-height: 120%;
        font-weight: 600;
        letter-spacing: 0.063em;
        text-transform: uppercase;
      }
      .stages__item-number {
        position: relative;
        z-index: 2;
        font-family: 'Buyan';
        font-size: 7em;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
    &-subgroup {
      margin: 0 auto;
    }
    &-icon {
      position: absolute;
      z-index: 2;
      top: 1.875em;
      right: 1.875em;
      max-width: 8.125em;
      img {
        width: 100%;
      }
    }
    &-text {
      position: relative;
      z-index: 2;
      font-size: 1.438em;
      line-height: 120%;
      font-weight: 800;
      letter-spacing: 0.058em;
    }
    .circle {
      position: absolute;
      top: 10.25em;
      left: 10.25em;
      width: 20em;
      height: 20em;
      display: block;
      background: #C6981D;
      border-radius: 50%;
      pointer-events: none;
    }
  }
}
