        max-width: 380px;
        align-items: start;
        padding-left: 120px;
        margin: 0;
      }
      &__number {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 90px;
        height: 90px;
        font-size: 36px;
        letter-spacing: 1.44px;
      }
      &__title {
        width: 100%;
        font-size: 17px;
      }
      &__text {
        width: 100%;
        font-size: 13px;
      }
      &__img {
        width: 100%;
        max-width: 100%;
        order: -1;
        img {
          border-radius: 10px;
        }
      }
    }
  }
  .questions {
    padding: 25px 0 50px;
    &__title {
      margin-bottom: 5px;
    }
    &__subtitle {
      font-size: 16px;
      margin-bottom: 30px;
    }
    &__slider {
      padding-bottom: 32px;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-pagination {
      &-bullet {
        width: 40px;
        height: 2.5px;
        margin: 0 5px !important;
      }
    }
  }
  .contacts {
    padding: 20px 0 10px;
    &__inner {
      gap: 30px;
    }
    &__title {
      margin-bottom: 5px;
    }
    &__items {
      gap: 10px;
    }
    &__item {
      padding-left: 5px;
      &:before {
        width: 1px;
      }
    }
    &__location,
    &__phone {
      font-size: 11px;
    }
    &__time,
    &__mail {
      font-size: 8px;
    }
    &__btn {
      width: max-content;
      height: 22px;
      border-radius: 4px;
      padding: 2px 6px;
      span {
        font-size: 13px;
        letter-spacing: -0.26px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    background-image: url("../img/main-img--mob.webp");
  }
}

@media screen and (max-width: 439px) {
  .stages {
    &__item {
      &-group {
        &:before {
          width: 9.089em;
          height: 9.089em;
        }
      }
      &-group2 {
        &:before {
          width: 9.089em;
          height: 9.089em;
        }
        &:after {
          width: 8.152em;
          height: 8.152em;
        }
      }
      &-icon {
        max-width: 2.5em;
      }
      .circle {
        top: 4.5445em;
        left: 4.5445em;
        width: 9.089em;
        height: 9.089em;
      }
    }
  }
  .connection {
    &__inner {
      grid-template-columns: 100%;
    }
  }
  .questions {
    .video {
      &__title {
        font-size: 28px;
      }
      &__title--1 {
        top: 30px;
      }
      &__title--2 {
        bottom: 30px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__title {
      font-size: 16px;
      span {
        font-size: 28px;
      }
    }
    &__subtitle {
      font-size: 16px;
      span {
        font-size: 33px;
        letter-spacing: 0.66px;
      }
    }
    &__text {
      font-size: 12px;
      letter-spacing: -0.24px;
    }
    &__list {
      gap: 12px;
      li {
        font-size: 12px;
        letter-spacing: -0.24px;
        &:before {
          width: 15px;
          height: 15px;
        }
      }
    }
    &__btn {
      span {
        font-size: 8px;
        letter-spacing: -0.16px;
      }
    }
    &__info {
      padding-left: 10px;
      font-size: 8px;
      letter-spacing: 1.12px;
    }
    &__founder {
      &-name {
        font-size: 12px;
        letter-spacing: -0.24px;
      }
      &-text {
        font-size: 7px;
        letter-spacing: 0.14px;
      }
    }
  }
  .info {
    &__video-wrap {
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__start,
    &__period,
    &__finish {
      font-size: 12px;
    }
  }
  .advantages {
    .quote {
      &__text {
        font-size: 13px;
        letter-spacing: -0.26px;
        margin-bottom: 10px;
      }
      &__author {
        font-size: 9px;
        letter-spacing: -0.18px;
      }
    }
  }
  .stages {
    .container {
      padding: 0 20px;
    }
  }
  .advantage-card2 {
    &__text2 {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
    &__list {
      li {
        font-size: 16px;
        letter-spacing: -0.32px;
        &:before {
          top: 7px;
        }
      }
    }
    &__btn {
      height: 40px;
      span {
        font-size: 12px;
        letter-spacing: -0.24px;
      }
    }
  }
  .variants {
    &__subtitle {
      font-size: 32px;
      letter-spacing: -0.64px;
      margin-bottom: 30px;
    }
    &__cards {
      gap: 30px;
      margin-bottom: 30px;
    }
  }
  .variant-card {
    &__title {
      font-size: 20px;
      letter-spacing: 0.8px;
    }
    &__subgroup {
      gap: 10px;
    }
    &__number {
      width: 40px;
      height: 40px;
      font-size: 20px;
      letter-spacing: 0.8px;
    }
    &__price {
      font-size: 32px;
      letter-spacing: 1.28px;
    }
    &__info {
      font-size: 20px;
      letter-spacing: 0.8px;
    }
    &__text {
      font-size: 16px;
    }
  }
  .info2 {
    &-card {
      &:nth-child(even) {
        .info2-card__desc {
          padding-right: 90px;
        }
      }
      &__desc {
        padding-left: 90px;
      }
      &__number {
        width: 70px;
        height: 70px;
        font-size: 30px;
        letter-spacing: 1.2px;
      }
    }
  }
  .contacts {
    &__inner {
      gap: 15px;
    }
  }
}