.footer {
  padding: 20px 0 40px;
  .container {
    max-width: 1680px;
  }
  &__inner {
    text-align: center;
    a {
      display: inline-block;
      font-size: 26px;
      line-height: 120%;
      font-weight: 300;
      &:hover {
        color: #C6981D;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    &__inner {
      a {
        font-size: 20px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      a {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 10px 0 15px;
    &__inner {
      a {
        font-size: 6px;
      }
    }
  }
}